import styled from '@emotion/styled';
import { SerializedStyles } from '@emotion/utils';
import { motion } from 'framer-motion';

type Types = {
  customCSS?: SerializedStyles;
};

export const AtomTableContainerStyled = styled(motion.main)`
  display: flex;
  flex-direction: column;
`;

export const AtomTableSectionStyled = styled(motion.section)`
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow-x: auto;
  ::-webkit-scrollbar {
    width: 7px;
    height: 7px;
  }
`;

type IAtomTableStyled = {
  cl: number;
  min?: number;
  customCSST?: SerializedStyles;
};

export const AtomTableStyled = styled(motion.table)<IAtomTableStyled>`
  display: flex;
  flex-direction: column;
  position: relative;
  overflow-y: hidden;
  overflow-x: scroll;
  font-family: 'Montserrat', sans-serif;
  height: max-content;
  border-radius: 4px;
  outline: 1px solid #eeeeee;
  color: ${({ color }) => color || `black`};
  ::-webkit-scrollbar {
    width: 7px;
    height: 7px;
  }
  overflow: auto;
  tbody {
    min-width: 100%;
    position: relative;
    tr {
      grid-template-columns: repeat(
        ${({ cl }) => cl ?? 1},
        minmax(${({ min }) => `${min ?? 150}px`}, 1fr)
      );
    }
    .head {
      position: sticky;
      top: 0;
      z-index: 100;
      th {
        font-family: 'Montserrat', sans-serif;
        text-align: left;
        color: #565656;
        font-size: 14px;
        font-weight: 700;
        padding: 15px 30px 15px 30px;
        span {
          font-size: 8px;
        }
      }
    }
  }
  ${({ customCSST }) => customCSST}
`;

export const AtomTheadStyled = styled(motion.thead)<Types>`
  top: 0;
  position: absolute;
  display: flex;
  width: max-content;
  position: sticky;
  flex-direction: column;
  font-family: 'Montserrat', sans-serif;
  height: fit-content;
  text-align: center;
  background-color: white;
  border-bottom: 1px solid #eeeeee;
  tr {
    display: grid;
    th {
      position: -webkit-sticky;
      position: sticky;
      top: 0;
      z-index: 2;

      font-family: 'Montserrat', sans-serif;
      text-align: left;
      color: #565656;
      font-size: 14px;
      font-weight: 700;
      padding: 15px 30px 15px 30px;
      span {
        font-size: 8px;
      }
    }
  }
  tr {
    background-color: #ffffff;
    transition: all 0.3s ease-in-out;
  }
  tr:nth-child(odd) {
    background-color: #fbfbfb;
  }
  tr:hover {
    background-color: #f0f0f0;
  }
`;

export const AtomTbodyStyled = styled(motion.tbody)<Types>`
  display: flex;
  width: max-content;
  flex-direction: column;

  tr {
    display: grid;
    background-color: #ffffff;
    transition: all 0.3s ease-in-out;
  }
  tr:nth-child(odd) {
    background-color: #fbfbfb;
  }
  tr:hover {
    background-color: #f0f0f0;
  }
`;

export const TRStyled = styled(motion.tr)<Types>`
  ${({ customCSS }) => customCSS};
`;

export const TDStyled = styled(motion.td)<Types>`
  font-family: 'Montserrat', sans-serif;
  color: #565656;
  font-size: 12px;
  font-weight: 500;
  padding: 15px 30px;
  text-align: left;
  border-bottom: 1px solid #eeeeee;
  ${({ customCSS }) => customCSS};
`;

export const AtomTheaderStyled = styled(motion.header)<Types>`
  font-family: 'Montserrat', sans-serif;
  color: #565656;
  font-size: 14px;
  padding: 15px 0px;
  font-weight: 700;
  text-align: left;
  border-bottom: 1px solid #eeeeee;
`;

export const AtomTfooterStyled = styled(motion.footer)<Types>`
  font-family: 'Montserrat', sans-serif;
  color: #565656;
  font-size: 14px;
  padding: 15px 30px;
  font-weight: 700;
  text-align: left;
  border-bottom: 1px solid #eeeeee;
`;
